.navBar {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    background-color: rgb(255, 0, 0);

    nav {
        height: 80px;
        width: 100%;
        z-index: 100;

        ul {
            float: right;
            margin-right: 20px;

            li {
                display: inline-block;
                margin-top: 10px;

                a {
                    font-family: Poppins;
                    color: white;
                    font-size: 1vw;
                    padding: 7px;
                    text-transform: uppercase;
                    text-decoration: none;

                }
            }
        }
    }

    label.logox {
        color: white;
        font-size: 35px;
        line-height: 80px;
        padding: 0 100px;
        font-weight: bold;

        a {
            background: none;

            img {
                width: 12%;
                margin-top: .1vw;
            }
        }
    }

    a.activex {
        background: #943e3e;
        transition: .5s;
        border-radius: 20px;
    }

    a {
        &:hover {
            background: #000000;
            transition: .5s;
            border-radius: 20px;
        }
    }

    .checkbtn {
        font-size: 30px;
        color: white;
        float: right;
        line-height: 80px;
        margin-right: 40px;
        cursor: pointer;
        display: none;
    }

    #check {
        display: none;
    }



    @media (max-width: 952px) {
        label.logox {
            font-size: 30px;
            padding-left: 50px;
        }

        nav {
            ul {
                li {
                    a {
                        font-size: 1.4vw;
                    }
                }
            }
        }
    }

    @media (max-width: 858px) {
        .checkbtn {
            display: block;
        }

        ul {
            position: fixed;
            width: 100%;
            height: 100vh;
            background: #272626;
            top: 80px;
            left: -110%;
            text-align: center;
            transition: all .5s;
            padding: 0;
        }

        nav {
            ul {
                li {
                    display: block;
                    margin: 50px 0;
                    line-height: 30px;

                    a {
                        font-size: 3vw;
                    }
                }
            }
        }

        a {
            &:hover {
                background: none;
                color: #000000;
            }
        }

        a.activex {
            background: none;
            color: #943e3e;
        }

        #check {
            &:checked {
                ~ {
                    ul {
                        left: 0;
                    }
                }
            }
        }
    }
}